
.file-uploads {
  overflow: hidden;
  position: relative;
  text-align: center;
  display: inline-block;
}
.file-uploads span {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.file-uploads input {
  z-index: 1;
  opacity: 0;
  font-size: 20em;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}
.file-uploads.file-uploads-html5 input {
  position: fixed;
  width: 1px !important;
  height: 1px !important;
  top: -999em !important;
  left: 0 !important;
  right: auto !important;
  bottom: auto !important;
}



